/**
 * Zustand Store ูุฅุฏุงุฑุฉ ุญุงูุฉ ุงููุตุงุฏูุฉ
 * ูุฑุจูุท ุจู Backend API
 */

import { create } from "zustand";
import { AuthState, LoginData, SignupData } from "@/types/auth";
import { authService } from "@/lib/api/services/authService";
import { tokenManager } from "@/lib/api/client";
import { ApiError } from "@/lib/api/client";

// ุฅูุดุงุก Store
export const useAuthStore = create<AuthState>((set) => ({
  // ุงูุญุงูุฉ ุงูุฃูููุฉ
  user: null,
  token: null,
  isAuthenticated: false,
  isLoading: false,
  error: null,

  // ุชุณุฌูู ุงูุฏุฎูู - ูุชุตู ุจู Backend API
  login: async (data: LoginData) => {
    set({ isLoading: true, error: null });

    try {
      const result = await authService.login(data);

      set({
        user: result.user,
        token: result.token,
        isAuthenticated: true,
        isLoading: false,
        error: null,
      });
    } catch (error) {
      // ุงุณุชุฎุฑุงุฌ ุฑุณุงูุฉ ุงูุฎุทุฃ
      const errorMessage =
        error instanceof ApiError
          ? error.message
          : error instanceof Error
            ? error.message
            : "ุญุฏุซ ุฎุทุฃ ุฃุซูุงุก ุชุณุฌูู ุงูุฏุฎูู";

      set({
        user: null,
        token: null,
        isAuthenticated: false,
        isLoading: false,
        error: errorMessage,
      });
      throw error;
    }
  },

  // ุฅูุดุงุก ุญุณุงุจ ุฌุฏูุฏ - ูุชุตู ุจู Backend API
  signup: async (data: SignupData) => {
    set({ isLoading: true, error: null });

    try {
      const result = await authService.register(data);

      set({
        user: result.user,
        token: result.token,
        isAuthenticated: true,
        isLoading: false,
        error: null,
      });
    } catch (error) {
      // ุงุณุชุฎุฑุงุฌ ุฑุณุงูุฉ ุงูุฎุทุฃ
      const errorMessage =
        error instanceof ApiError
          ? error.message
          : error instanceof Error
            ? error.message
            : "ุญุฏุซ ุฎุทุฃ ุฃุซูุงุก ุฅูุดุงุก ุงูุญุณุงุจ";

      set({
        user: null,
        token: null,
        isAuthenticated: false,
        isLoading: false,
        error: errorMessage,
      });
      throw error;
    }
  },

  // ุชุณุฌูู ุงูุฎุฑูุฌ - ูุชุตู ุจู Backend API
  logout: async () => {
    set({ isLoading: true });

    try {
      await authService.logout();
    } finally {
      // ูุณุญ ุงูุญุงูุฉ ูู ุฌููุน ุงูุญุงูุงุช (ุญุชู ูู ูุดู ุงูุทูุจ)
      set({
        user: null,
        token: null,
        isAuthenticated: false,
        isLoading: false,
        error: null,
      });
    }
  },

  // ุงูุชุญูู ูู ุงูุฌูุณุฉ ุงูุญุงููุฉ - ูุชุตู ุจู Backend API
  checkAuth: async () => {
    // ุงูุชุญูู ูู ูุฌูุฏ ุชููู ูุญููุธ
    if (!tokenManager.hasValidToken()) {
      set({
        user: null,
        token: null,
        isAuthenticated: false,
        isLoading: false,
        error: null,
      });
      return;
    }

    set({ isLoading: true });

    try {
      // ุฌูุจ ุจูุงูุงุช ุงููุณุชุฎุฏู ูู ุงูู API
      const user = await authService.getCurrentUser();
      const token = tokenManager.getAccessToken();

      set({
        user,
        token,
        isAuthenticated: true,
        isLoading: false,
        error: null,
      });
    } catch {
      // ูุดู ุงูุชุญูู - ูุณุญ ุงูุชููู ูุงูุญุงูุฉ
      tokenManager.clearTokens();
      set({
        user: null,
        token: null,
        isAuthenticated: false,
        isLoading: false,
        error: null,
      });
    }
  },

  // ูุณุญ ุงูุฃุฎุทุงุก
  clearError: () => {
    set({ error: null });
  },
}));
