/**
 * Get display subtitle/description for a component
 */
export function getComponentDisplaySubtitle(component: Component): string {
  const settings = component.settings as any;

  switch (component.type) {
    case "question":
      return getQuestionTypeLabel(settings.questionType) + (settings.required ? " โข ูุทููุจ" : "");

    case "rating":
      return getRatingTypeLabel(settings.ratingType) + (settings.required ? " โข ูุทููุจ" : "");

    case "table":
      return getTableTypeLabel(settings.tableType) + ` โข ${settings.columns?.length || 0} ุนููุฏ ร ${settings.rowCount || 0} ุตู`;

    case "pdf_upload":
    case "image_upload":
    case "video_upload":
      return `ุญุฌู ุฃูุตู: ${settings.maxFileSize || 10}MB` + (settings.allowMultiple ? " โข ูููุงุช ูุชุนุฏุฏุฉ" : "");

    case "link":
      return "ุฑุงุจุท ุฎุงุฑุฌู" + (settings.validateUrl ? " โข ูุน ุงูุชุญูู" : "");

    case "signature":
      return `${settings.width || 500}ร${settings.height || 200}px`;

    case "text":
      const content = settings.content || "";
      return content.length > 60 ? content.substring(0, 60) + "..." : content;

    case "display":
      return getDisplayTypeLabel(settings.displayType);

    // Support question subtypes directly (for templates)
    case "short_text" as any:
    case "long_text" as any:
    case "single_choice" as any:
    case "multiple_choice" as any:
    case "dropdown" as any:
    case "yes_no" as any:
    case "linear_scale" as any:
    case "choice_grid" as any:
    case "number" as any:
    case "email" as any:
    case "phone" as any:
    case "date" as any:
    case "time" as any:
      return getQuestionTypeLabel(component.type as string) + (settings.required ? " โข ูุทููุจ" : "");

    default:
      return "";
  }
}

