// ูุธุงู ุญุณุงุจ ุงูุฏุฑุฌุงุช ูุงูุชุตุญูุญ ุงูุชููุงุฆู

import { Event } from "@/types/event";
import { ComponentAnswer, ResponseScore } from "@/types/response";

/**
 * ุญุณุงุจ ุฏุฑุฌุฉ ุงูุงุฎุชุจุงุฑ ุจูุงุกู ุนูู ุงูุฅุฌุงุจุงุช
 */
export function calculateScore(
  event: Event,
  answers: ComponentAnswer[]
): ResponseScore {
  let totalPoints = 0;
  let earnedPoints = 0;
  let correctAnswersCount = 0;
  let totalQuestionsWithGrading = 0;

  // ุงููุฑูุฑ ุนูู ุฌููุน ุงูุฃูุณุงู ูุงูููููุงุช
  event.sections.forEach((section) => {
    section.components.forEach((component) => {
      const settings = component.settings as any;

      // ุชุญูู ูู ูุฌูุฏ ุชุตุญูุญ ุชููุงุฆู (points ูุญุฏุฏุฉ)
      if (settings.points !== undefined && settings.points > 0) {
        totalQuestionsWithGrading++;
        totalPoints += settings.points;

        // ุงูุจุญุซ ุนู ุฅุฌุงุจุฉ ุงููุดุงุฑู ููุฐุง ุงููููู
        const answer = answers.find((a) => a.componentId === component.id);

        if (answer) {
          // ุชุญูู ูู ุตุญุฉ ุงูุฅุฌุงุจุฉ ุจูุงุกู ุนูู ููุน ุงูุณุคุงู
          const isCorrect = checkAnswer(
            settings.questionType,
            answer.answer,
            settings.correctAnswer
          );

          if (isCorrect) {
            earnedPoints += settings.points;
            correctAnswersCount++;
          }

          // ุชุญุฏูุซ ูุนูููุงุช ุงูุฅุฌุงุจุฉ
          answer.isCorrect = isCorrect;
          answer.pointsEarned = isCorrect ? settings.points : 0;
        }
      }
    });
  });

  // ุญุณุงุจ ุงููุณุจุฉ ุงููุฆููุฉ
  const percentage = totalPoints > 0 ? (earnedPoints / totalPoints) * 100 : 0;

  // ุชุญุฏูุฏ ุญุงูุฉ ุงููุฌุงุญ (ูููู ุชุฎุตูุตูุง ูุงุญูุงู)
  const passed = percentage >= 50; // ุงููุฌุงุญ ุฅุฐุง ูุงูุช ุงููุณุจุฉ 50% ุฃู ุฃูุซุฑ

  // ุชุญุฏูุฏ ุงูุฏุฑุฌุฉ (Grade)
  const grade = getGrade(percentage);

  return {
    totalPoints,
    earnedPoints,
    percentage: Math.round(percentage * 100) / 100, // ุชูุฑูุจ ูุฑูููู ุนุดุฑููู
    grade,
    passed,
  };
}

/**
 * ุงูุชุญูู ูู ุตุญุฉ ุงูุฅุฌุงุจุฉ ุจูุงุกู ุนูู ููุน ุงูุณุคุงู
 */
function checkAnswer(
  questionType: string,
  userAnswer: any,
  correctAnswer: any
): boolean {
  if (!correctAnswer || userAnswer === undefined || userAnswer === null) {
    return false;
  }

  switch (questionType) {
    case "single_choice":
    case "yes_no":
      // ููุงุฑูุฉ ูุจุงุดุฑุฉ
      return String(userAnswer).toLowerCase() === String(correctAnswer).toLowerCase();

    case "multiple_choice":
      // ููุงุฑูุฉ ุงููุตูููุงุช
      if (!Array.isArray(userAnswer) || !Array.isArray(correctAnswer)) {
        return false;
      }
      // ูุฌุจ ุฃู ุชููู ููุณ ุงูุทูู ูููุณ ุงูุนูุงุตุฑ
      if (userAnswer.length !== correctAnswer.length) {
        return false;
      }
      // ุชุฑุชูุจ ุงููุตูููุงุช ูููุงุฑูุชูุง
      const sortedUser = [...userAnswer].sort();
      const sortedCorrect = [...correctAnswer].sort();
      return sortedUser.every((val, idx) => 
        String(val).toLowerCase() === String(sortedCorrect[idx]).toLowerCase()
      );

    case "short_text":
    case "long_text":
      // ููุงุฑูุฉ ุงููุตูุต (case-insensitive ูุฅุฒุงูุฉ ุงููุณุงูุงุช ุงูุฒุงุฆุฏุฉ)
      const userText = String(userAnswer).trim().toLowerCase();
      const correctText = String(correctAnswer).trim().toLowerCase();
      return userText === correctText;

    case "number":
      // ููุงุฑูุฉ ุงูุฃุฑูุงู
      return Number(userAnswer) === Number(correctAnswer);

    default:
      return false;
  }
}

/**
 * ุชุญุฏูุฏ ุงูุฏุฑุฌุฉ (Grade) ุจูุงุกู ุนูู ุงููุณุจุฉ ุงููุฆููุฉ
 */
function getGrade(percentage: number): string {
  if (percentage >= 95) return "A+";
  if (percentage >= 90) return "A";
  if (percentage >= 85) return "B+";
  if (percentage >= 80) return "B";
  if (percentage >= 75) return "C+";
  if (percentage >= 70) return "C";
  if (percentage >= 65) return "D+";
  if (percentage >= 60) return "D";
  if (percentage >= 50) return "E";
  return "F";
}

