/**
 * ุญุณุงุจ ุฅุญุตุงุฆูุงุช ุชูุตูููุฉ ููุงุฎุชุจุงุฑ
 */
export function calculateDetailedStats(
  event: Event,
  answers: ComponentAnswer[]
): {
  totalQuestions: number;
  answeredQuestions: number;
  correctAnswers: number;
  incorrectAnswers: number;
  skippedQuestions: number;
} {
  let totalQuestions = 0;
  let answeredQuestions = 0;
  let correctAnswers = 0;
  let incorrectAnswers = 0;

  event.sections.forEach((section) => {
    section.components.forEach((component) => {
      const settings = component.settings as any;

      // ุนุฏ ุงูุฃุณุฆูุฉ ุงูุชู ููุง ุชุตุญูุญ ุชููุงุฆู ููุท
      if (settings.points !== undefined && settings.points > 0) {
        totalQuestions++;

        const answer = answers.find((a) => a.componentId === component.id);

        if (answer && answer.answer !== undefined && answer.answer !== null && answer.answer !== "") {
          answeredQuestions++;

          if (answer.isCorrect) {
            correctAnswers++;
          } else {
            incorrectAnswers++;
          }
        }
      }
    });
  });

  const skippedQuestions = totalQuestions - answeredQuestions;

  return {
    totalQuestions,
    answeredQuestions,
    correctAnswers,
    incorrectAnswers,
    skippedQuestions,
  };
}

