/**
 * Zustand Store ูุฅุฏุงุฑุฉ ุงูุฃุญุฏุงุซ
 * ูุฑุจูุท ุจู Backend API
 */

import { create } from "zustand";
import { Event, EventFormData, EventsState, EventStatus, EventType } from "@/types/event";
import { eventsService } from "@/lib/api/services/eventsService";
import { ApiError } from "@/lib/api/client";

export const useEventsStore = create<EventsState>((set, get) => ({
  events: [],
  currentEvent: null,
  isLoading: false,
  error: null,

  filters: {
    search: "",
    status: "all",
    type: "all",
    sortBy: "createdAt",
    sortOrder: "desc",
  },

  // ุฌูุจ ุฌููุน ุงูุฃุญุฏุงุซ - ูุชุตู ุจู Backend API
  fetchEvents: async () => {
    set({ isLoading: true, error: null });
    try {
      const events = await eventsService.getAll();
      set({ events, isLoading: false });
    } catch (error) {
      const errorMessage =
        error instanceof ApiError
          ? error.message
          : error instanceof Error
            ? error.message
            : "ุญุฏุซ ุฎุทุฃ ุฃุซูุงุก ุฌูุจ ุงูุฃุญุฏุงุซ";
      set({
        error: errorMessage,
        isLoading: false,
      });
    }
  },

  // ุฌูุจ ุญุฏุซ ุจูุงุณุทุฉ ID - ูุชุตู ุจู Backend API
  // ูุณุชุฎุฏู /full endpoint ูุฌูุจ ุงูุฃูุณุงู ูุงูููููุงุช
  fetchEventById: async (id: string) => {
    set({ isLoading: true, error: null });
    try {
      // ุงุณุชุฎุฏุงู getFullDetails ุจุฏูุงู ูู getById ูุฌูุจ ุงูุฃูุณุงู ูุงูููููุงุช
      const event = await eventsService.getFullDetails(id);
      set({ currentEvent: event, isLoading: false });
    } catch (error) {
      const errorMessage =
        error instanceof ApiError
          ? error.message
          : error instanceof Error
            ? error.message
            : "ุญุฏุซ ุฎุทุฃ ุฃุซูุงุก ุฌูุจ ุงูุญุฏุซ";
      set({
        error: errorMessage,
        isLoading: false,
      });
    }
  },

  // ุฌูุจ ุญุฏุซ ุจูุงุณุทุฉ ุฑูุฒ ุงููุดุงุฑูุฉ (ูููุดุงุฑููู) - Public endpoint
  fetchEventByShareCode: async (shareCode: string) => {
    set({ isLoading: true, error: null });
    try {
      const event = await eventsService.getByShareCode(shareCode);
      set({ currentEvent: event, isLoading: false });
    } catch (error) {
      const errorMessage =
        error instanceof ApiError
          ? error.message
          : error instanceof Error
            ? error.message
            : "ุญุฏุซ ุฎุทุฃ ุฃุซูุงุก ุฌูุจ ุงูุญุฏุซ";
      set({
        error: errorMessage,
        isLoading: false,
      });
    }
  },

  // ุฅูุดุงุก ุญุฏุซ ุฌุฏูุฏ - ูุชุตู ุจู Backend API
  // ูุณุชุฎุฏู createWithSections ูุญูุธ ุงูุญุฏุซ ูุน ุงูุฃูุณุงู ูุงูููููุงุช
  createEvent: async (data: Event | EventFormData) => {
    set({ isLoading: true, error: null });
    try {
      let newEvent: Event;

      // ุฅุฐุง ูุงู ุงูุญุฏุซ ูุญุชูู ุนูู ุฃูุณุงูุ ุงุณุชุฎุฏู createWithSections
      if ('sections' in data && data.sections && data.sections.length > 0) {
        newEvent = await eventsService.createWithSections(data as Event);
      } else {
        // ูุฅูุง ุงุณุชุฎุฏู create ุงูุนุงุฏู
        newEvent = await eventsService.create(data as EventFormData);
      }

      set((state) => ({
        events: [newEvent, ...state.events],
        isLoading: false,
      }));

      return newEvent;
    } catch (error) {
      const errorMessage =
        error instanceof ApiError
          ? error.message
          : error instanceof Error
            ? error.message
            : "ุญุฏุซ ุฎุทุฃ ุฃุซูุงุก ุฅูุดุงุก ุงูุญุฏุซ";
      set({
        error: errorMessage,
        isLoading: false,
      });
      throw error;
    }
  },

  // ุชุญุฏูุซ ุญุฏุซ - ูุชุตู ุจู Backend API
  updateEvent: async (id: string, data: Partial<EventFormData>) => {
    set({ isLoading: true, error: null });
    try {
      const updatedEvent = await eventsService.update(id, data);

      set((state) => ({
        events: state.events.map((e) => (e.id === id ? updatedEvent : e)),
        currentEvent:
          state.currentEvent?.id === id ? updatedEvent : state.currentEvent,
        isLoading: false,
      }));
    } catch (error) {
      const errorMessage =
        error instanceof ApiError
          ? error.message
          : error instanceof Error
            ? error.message
            : "ุญุฏุซ ุฎุทุฃ ุฃุซูุงุก ุชุญุฏูุซ ุงูุญุฏุซ";
      set({
        error: errorMessage,
        isLoading: false,
      });
      throw error;
    }
  },

  // ุญุฐู ุญุฏุซ - ูุชุตู ุจู Backend API
  deleteEvent: async (id: string) => {
    set({ isLoading: true, error: null });
    try {
      await eventsService.delete(id);

      set((state) => ({
        events: state.events.filter((e) => e.id !== id),
        isLoading: false,
      }));
    } catch (error) {
      const errorMessage =
        error instanceof ApiError
          ? error.message
          : error instanceof Error
            ? error.message
            : "ุญุฏุซ ุฎุทุฃ ุฃุซูุงุก ุญุฐู ุงูุญุฏุซ";
      set({
        error: errorMessage,
        isLoading: false,
      });
      throw error;
    }
  },

  // ูุณุฎ ุญุฏุซ - ูุชุตู ุจู Backend API
  duplicateEvent: async (id: string) => {
    set({ isLoading: true, error: null });
    try {
      const duplicatedEvent = await eventsService.duplicate(id);

      set((state) => ({
        events: [duplicatedEvent, ...state.events],
        isLoading: false,
      }));

      return duplicatedEvent;
    } catch (error) {
      const errorMessage =
        error instanceof ApiError
          ? error.message
          : error instanceof Error
            ? error.message
            : "ุญุฏุซ ุฎุทุฃ ุฃุซูุงุก ูุณุฎ ุงูุญุฏุซ";
      set({
        error: errorMessage,
        isLoading: false,
      });
      throw error;
    }
  },

  // ุฃุฑุดูุฉ ุญุฏุซ - ูุชุตู ุจู Backend API (ุชุญุฏูุซ ุงูุญุงูุฉ ุฅูู archived)
  archiveEvent: async (id: string) => {
    set({ isLoading: true, error: null });
    try {
      // ุงุณุชุฎุฏุงู updateStatus ูุชุบููุฑ ุงูุญุงูุฉ ุฅูู archived ุฏูู ููุฏุงู ุงูุจูุงูุงุช
      const updatedEvent = await eventsService.updateStatus(id, "archived");

      set((state) => ({
        events: state.events.map((e) =>
          e.id === id ? updatedEvent : e
        ),
        currentEvent:
          state.currentEvent?.id === id ? updatedEvent : state.currentEvent,
        isLoading: false,
      }));
    } catch (error) {
      const errorMessage =
        error instanceof ApiError
          ? error.message
          : error instanceof Error
            ? error.message
            : "ุญุฏุซ ุฎุทุฃ ุฃุซูุงุก ุฃุฑุดูุฉ ุงูุญุฏุซ";
      set({
        error: errorMessage,
        isLoading: false,
      });
      throw error;
    }
  },

  // ุชุบููุฑ ุญุงูุฉ ุงูุญุฏุซ - ูุชุตู ุจู Backend API
  // ูุณุชุฎุฏู PATCH endpoint ูุชุญุฏูุซ ุงูุญุงูุฉ ููุท ุฏูู ุงููุณุงุณ ุจุงูุจูุงูุงุช ุงูุฃุฎุฑู
  updateEventStatus: async (id: string, status: EventStatus) => {
    set({ isLoading: true, error: null });
    try {
      // ุงุณุชุฎุฏุงู updateStatus ุจุฏูุงู ูู update ูุชุฌูุจ ููุฏุงู ุงูุจูุงูุงุช
      const updatedEvent = await eventsService.updateStatus(id, status);

      set((state) => ({
        events: state.events.map((e) => (e.id === id ? updatedEvent : e)),
        currentEvent:
          state.currentEvent?.id === id ? updatedEvent : state.currentEvent,
        isLoading: false,
      }));
    } catch (error) {
      const errorMessage =
        error instanceof ApiError
          ? error.message
          : error instanceof Error
            ? error.message
            : "ุญุฏุซ ุฎุทุฃ ุฃุซูุงุก ุชุญุฏูุซ ุญุงูุฉ ุงูุญุฏุซ";
      set({
        error: errorMessage,
        isLoading: false,
      });
      throw error;
    }
  },

  // ุฅูุดุงุก ุญุฏุซ ูู ุงูุตูุฑ - ูุณุชุฎุฏู createEvent
  createEventFromScratch: async (data) => {
    // ุชุญููู ุงูุจูุงูุงุช ุฅูู EventFormData
    const eventData: EventFormData = {
      title: data.title,
      description: data.description,
      type: data.type,
      status: "draft",
      coverImage: data.coverImage,
      settings: {},
    };
    return get().createEvent(eventData);
  },

  // ุชุญุฏูุซ ุฅุนุฏุงุฏุงุช ุงูุญุฏุซ - ูุณุชุฎุฏู updateEvent
  updateEventSettings: async (id: string, settings) => {
    return get().updateEvent(id, { settings });
  },

  // ูุดุฑ ุญุฏุซ - ูุชุตู ุจู Backend API
  publishEvent: async (id: string) => {
    set({ isLoading: true, error: null });
    try {
      const publishedEvent = await eventsService.publish(id);

      set((state) => ({
        events: state.events.map((e) =>
          e.id === id ? publishedEvent : e
        ),
        currentEvent:
          state.currentEvent?.id === id ? publishedEvent : state.currentEvent,
        isLoading: false,
      }));
    } catch (error) {
      const errorMessage =
        error instanceof ApiError
          ? error.message
          : error instanceof Error
            ? error.message
            : "ุญุฏุซ ุฎุทุฃ ุฃุซูุงุก ูุดุฑ ุงูุญุฏุซ";
      set({
        error: errorMessage,
        isLoading: false,
      });
      throw error;
    }
  },
  
  // ุชุนููู ุงูุจุญุซ
  setSearch: (search: string) => {
    set((state) => ({
      filters: { ...state.filters, search },
    }));
  },
  
  // ุชุนููู ููุชุฑ ุงูุญุงูุฉ
  setStatusFilter: (status: EventStatus | "all") => {
    set((state) => ({
      filters: { ...state.filters, status },
    }));
  },
  
  // ุชุนููู ููุชุฑ ุงูููุน
  setTypeFilter: (type: EventType | "all") => {
    set((state) => ({
      filters: { ...state.filters, type },
    }));
  },
  
  // ุชุนููู ุงูุชุฑุชูุจ
  setSortBy: (sortBy: "createdAt" | "updatedAt" | "title" | "responses") => {
    set((state) => ({
      filters: { ...state.filters, sortBy },
    }));
  },
  
  // ุชุนููู ุงุชุฌุงู ุงูุชุฑุชูุจ
  setSortOrder: (sortOrder: "asc" | "desc") => {
    set((state) => ({
      filters: { ...state.filters, sortOrder },
    }));
  },
  
  // ูุณุญ ุงูููุงุชุฑ
  clearFilters: () => {
    set({
      filters: {
        search: "",
        status: "all",
        type: "all",
        sortBy: "createdAt",
        sortOrder: "desc",
      },
    });
  },
  
  // ุงูุญุตูู ุนูู ุงูุฃุญุฏุงุซ ุงููููุชุฑุฉ
  getFilteredEvents: () => {
    const { events, filters } = get();

    let filtered = [...events];

    // ุฅุฎูุงุก ุงูุฃุญุฏุงุซ ุงููุคุฑุดูุฉ ุชููุงุฆูุงู (ุฅูุง ุฅุฐุง ุทูุจ ุงููุณุชุฎุฏู ุนุฑุถูุง ุจุดูู ุตุฑูุญ)
    if (filters.status !== "archived") {
      filtered = filtered.filter((e) => e.status !== "archived");
    }

    // ููุชุฑุฉ ุญุณุจ ุงูุจุญุซ
    if (filters.search) {
      const searchLower = filters.search.toLowerCase();
      filtered = filtered.filter(
        (e) =>
          e.title.toLowerCase().includes(searchLower) ||
          e.description.toLowerCase().includes(searchLower)
      );
    }

    // ููุชุฑุฉ ุญุณุจ ุงูุญุงูุฉ (ุจุนุฏ ุฅุฎูุงุก ุงููุคุฑุดูุฉ)
    if (filters.status !== "all" && filters.status !== "archived") {
      filtered = filtered.filter((e) => e.status === filters.status);
    }
    
    // ููุชุฑุฉ ุญุณุจ ุงูููุน
    if (filters.type !== "all") {
      filtered = filtered.filter((e) => e.type === filters.type);
    }
    
    // ุงูุชุฑุชูุจ
    filtered.sort((a, b) => {
      let aValue: any;
      let bValue: any;
      
      switch (filters.sortBy) {
        case "title":
          aValue = a.title.toLowerCase();
          bValue = b.title.toLowerCase();
          break;
        case "responses":
          aValue = a.stats.totalResponses;
          bValue = b.stats.totalResponses;
          break;
        case "updatedAt":
          aValue = new Date(a.updatedAt).getTime();
          bValue = new Date(b.updatedAt).getTime();
          break;
        case "createdAt":
        default:
          aValue = new Date(a.createdAt).getTime();
          bValue = new Date(b.createdAt).getTime();
          break;
      }
      
      if (filters.sortOrder === "asc") {
        return aValue > bValue ? 1 : -1;
      } else {
        return aValue < bValue ? 1 : -1;
      }
    });
    
    return filtered;
  },
}));

